@keyframes show-alert {
  0% {
    transform: translate(-50%, -50%) scale(0);
  }
  100% {
    transform: translate(-50%, -50%) scale(1);
  }
}
* {
  margin: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
}

.container {
  max-width: 1440px;
  margin: 20px;
}
@media screen and (min-width: 580px) {
  .container {
    margin: 20px 40px;
  }
}
@media screen and (min-width: 1024px) {
  .container {
    margin: 0 auto;
  }
}

.module {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 50px;
  position: relative;
}
@media screen and (min-width: 780px) {
  .module {
    flex-direction: row;
    gap: 95px;
    margin-top: 96px;
  }
}
.module__image {
  margin-top: 80px;
}
@media screen and (min-width: 780px) {
  .module__image {
    flex-basis: 55%;
  }
}
@media screen and (min-width: 1024px) {
  .module__image {
    flex-basis: 35%;
    margin: 0;
  }
}
.module__image img {
  width: 100%;
}
@media screen and (min-width: 780px) {
  .module__content {
    margin-top: 80px;
    flex-basis: 40%;
  }
}
@media screen and (min-width: 1024px) {
  .module__content {
    margin-top: 0;
    flex-basis: 26%;
  }
}
.module__title {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 42px;
  color: #121212;
  margin-bottom: 24px;
}
@media screen and (min-width: 1024px) {
  .module__title {
    position: static;
  }
}
.module__text {
  font-weight: 300;
  line-height: 150%;
  margin-bottom: 32px;
}
.module__button {
  display: block;
  height: 48px;
  width: 80%;
  margin: 0 auto;
  text-align: center;
  border-radius: 32px;
  background: #E4C1AE;
  color: #fff;
  border: none;
  font-weight: 700;
  cursor: pointer;
}
@media screen and (min-width: 780px) {
  .module__button {
    margin: 0;
    width: 60%;
  }
}
@media screen and (min-width: 1024px) {
  .module__button {
    width: 128px;
  }
}

.alert-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(18, 18, 18, 0.5);
  display: block;
}
.alert-overlay--hide {
  display: none;
}

.alert {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(1);
  background-color: #fff;
  width: 80%;
  animation: show-alert 0.4s;
}
@media screen and (min-width: 1024px) {
  .alert {
    width: 920px;
    height: 210px;
  }
}
.alert__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 30px 10px 30px 20px;
  height: 100%;
}
@media screen and (min-width: 1024px) {
  .alert__wrapper {
    padding: 0 0 0 95px;
  }
}
.alert__close {
  position: absolute;
  top: 46px;
  right: 46px;
  width: 16px;
  height: 16px;
  cursor: pointer;
}
.alert__close::before, .alert__close::after {
  content: "";
  position: absolute;
  left: 8px;
  top: 0;
  height: 100%;
  width: 2px;
  background-color: #000;
  transform-origin: center center;
}
.alert__close::before {
  transform: rotate(45deg);
}
.alert__close::after {
  transform: rotate(-45deg);
}
.alert__title {
  font-size: 42px;
  color: #121212;
  margin-bottom: 24px;
}
.alert__content {
  display: flex;
  gap: 20px;
  align-items: center;
}
.alert__description {
  color: #121212;
  line-height: 24px;
}
.alert__button {
  display: block;
  height: 48px;
  width: 128px;
  text-align: center;
  background-color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 32px;
  text-transform: uppercase;
  color: #E4C1AE;
  letter-spacing: 2px;
  transition: 0.3s;
}
.alert__button:hover {
  background-color: #E4C1AE;
  color: #fff;
}